export const EVENT_TRACKING = Symbol('EVENT_TRACKING');

export enum TrackingType {
  // 输入字符
  inputText = 'input_text',
  // 输入法输入
  IMEInputText = 'ime_input_text',
  // 删除字符
  deleteText = 'delete_text',
  // 换行
  splitBlock = 'split_block',
  // 鼠标点选文字
  focusText = 'focus_text',
  // 鼠标托选文字
  selectText = 'select_text',
  // 全选文字
  selectAll = 'select_all',
  // 复制
  copy = 'copy',
  // 剪切
  cut = 'cut',
  // 粘贴
  paste = 'paste',
}

export interface TrackingData {
  type: TrackingType;
}

export function eventTracking(data: TrackingData) {
  return {
    type: EVENT_TRACKING,
    payload: data,
  };
}
